<template>
	<div id="delicious">
		<div id="deliciousPane" v-proportion='0.8'>
			<div id="deliciousLeft" v-proportion='1.8'>
				<div  :id="thisIndex==index?'deliciousLeftItem1':'deliciousLeftItem'" v-for="(item,index) in datas" :key='index' @click="clicked(index)">
					<div id="deliciousItemContent">{{item.name}}</div>
				</div>
			</div>
			<transition name="fade">
				<div id="deliciousRight" v-proportion='0.9'>
					<div id="deliciousRightTop">
						<div id="deliciousRightImg" v-proportion='0.8' :style="{backgroundImage:'url(' + thisContent.img + ')'}"></div>
						<div id="deliciousRightTitle">{{thisContent.name}}</div>
					</div>
					<div id="deliciousRightContent">{{thisContent.word}}</div>
				</div>
			</transition>
		</div>
	</div>
</template>
<script>
	export default{
		name :'delicious',
		data(){
			return{
				datas:[
					{id:1,img:require('../../../assets/delicious/del1.jpg'),name:'兰州拉面',word:"兰州拉面，兰州牛肉面，又称兰州清汤牛肉面，是“中国十大面条”之一，是甘肃省兰州地区的风味小吃。它以“汤镜者清，肉烂者香，面细者精”的独特风味和“一清二白三红四绿五黄”，一清（汤清）、二白（萝卜白）、三红（辣椒油红）、四绿（香菜、蒜苗绿）、五黄（面条黄亮），赢得了国内乃至全世界顾客的好评。并被中国烹饪协会评为三大中式快餐之一，得到美誉“中华第一面”。"},
					{id:2,img:require('../../../assets/delicious/del2.jpg'),name:'北京烤鸭',word:"北京烤鸭，烤鸭是具有世界声誉的北京著名菜式，起源于中国南北朝时期，《食珍录》中已记有炙鸭，在当时是宫廷食品。用料为优质肉食鸭北京鸭，果木炭火烤制，色泽红润，肉质肥而不腻，外脆里嫩。北京烤鸭分为两大流派，而北京最著名的烤鸭店也即是两派的代表。它以色泽红艳，肉质细嫩，味道醇厚，肥而不腻的特色，被誉为“天下美味”。"},
					{id:3,img:require('../../../assets/delicious/del3.jpg'),name:'上海糯米团',word:"上海糯米团，糯米团是江南地区经典的地方小吃。老上海人的传统早餐，在宁波和上海的一些地摊上，均可以见到这些特色的传统早餐。糯米擀成薄薄的一张面饼，在上面放点榨菜，夹一根油条，卷成一个团子，就这样，纯正的上海糯米团就完成了。"},
					{id:4,img:require('../../../assets/delicious/del4.jpg'),name:'重庆酸辣粉',word:"重庆酸辣粉，重庆酸辣粉是重庆城区广为流传的一种地方传统名小吃，历来就是重庆人的最爱之一 。手工制作的主粉由红薯，豌豆淀粉为主要原料，然后由农家用传统手工漏制。重庆酸辣粉的粉丝劲道弹牙、口味麻辣酸爽、浓香开胃，深受全国人民喜爱的重庆地方小吃。"},
					{id:5,img:require('../../../assets/delicious/del5.jpg'),name:'武汉热干面',word:"武汉热干面，热干面是中国十大面条之一，是湖北武汉最出名的小吃之一，有多种做法。以油、盐、芝麻酱、色拉油、香油、细香葱、大蒜子、量卤水汁、生抽为辅助材料。其色泽黄而油润，味道鲜美，由于热量高，也可以当作主食，营养早餐，补充机体所需的能量。"},
					{id:6,img:require('../../../assets/delicious/del6.jpg'),name:'宁波汤圆',word:"宁波汤圆，汤圆是浙江宁波著名的特色小吃之一，也是中国的代表小吃之一，春节，元宵节节日食俗。历史十分悠久。据传，汤圆起源于宋朝。当时明州（现浙江宁波市）兴起吃一种新奇食品，即用黑芝麻、猪脂肪油、少许白砂糖做馅，外面用糯米粉搓成球，煮熟后，吃起来香甜可口，饶有风趣。因为这种糯米球煮在锅里又浮又沉，所以它最早叫“浮元子”，后来有的地区把“浮元子”改称元宵。与大多数中国人不同，宁波人在春节早晨都有合家聚坐共进汤圆的传统习俗。"},
					{id:7,img:require('../../../assets/delicious/del7.jpg'),name:'广西桂林米粉',word:"广西桂林米粉，桂林米粉是广西桂林地区历史悠久的特色传统名小吃。以其独特的风味远近闻名。其做工考究，先将上好的早籼米磨成浆，装袋滤干，揣成粉团煮熟后压榨成圆根或片状即成。圆的称米粉，片状的称切粉，通称米粉，其特点是洁白、细嫩、软滑、爽口。其吃法多样。制作最最讲究卤水的制作，其工艺各家有异，大致以猪、牛骨、罗汉果和各式佐料熬煮而成，香味浓郁。卤水的用料和做法不同，米粉的风味也不同。大致有生菜粉、牛腩粉、三鲜粉、原汤粉、卤菜粉、酸辣粉、马肉米粉、担子米粉等。"},
					{id:8,img:require('../../../assets/delicious/del8.jpg'),name:'宁夏手抓羊肉',word:"宁夏手抓羊肉，宁夏手抓羊肉是一款宁夏菜品，制作原料主要有羊肉、 花椒等。"},
					{id:9,img:require('../../../assets/delicious/del9.jpg'),name:'西安羊肉泡馍',word:"西安羊肉泡馍，羊肉泡馍是陕西的风味美食，尤以西安最享盛名。它烹制精细，料重味醇，肉烂汤浓，肥而不腻，营养丰富，香气四溢，诱人食欲，食后回味无穷。因它暖胃耐饥，素为西安和西北地区各族人民所喜爱，外宾来陕也争先品尝，以饱口福。新中国成立以来，特别是近年来用以招待国际友人，也深受好评。羊肉泡馍已成为陕西名食的“总代表”。"},
					{id:10,img:require('../../../assets/delicious/del10.jpg'),name:'河南糊辣汤',word:"糊辣汤，胡辣汤，又名糊辣汤，中原知名小吃，起源于河南省周口市西华县逍遥镇及漯河舞阳县北舞渡镇，是中国北方早餐中常见的传统汤类名吃。胡辣汤的主要制作材料有胡辣汤料、胡椒、辣椒、熟羊（牛）肉、羊骨高汤、面筋、面粉、粉条、黄花菜、木耳等。"},
					{id:11,img:require('../../../assets/delicious/del11.jpg'),name:'四川串串香',word:"串串香起源于四川乐山，是四川地区特色传统小吃之一，也是草根美食最大众化的体现，它实际上是火锅的另一种形式，所以人们又往往称其为小火锅。“串串香”名字的由来是因为这是一种以竹签串上各种菜，将其放进滚烫的火锅中涮着吃的小吃。"}
				],
				thisIndex:0,
				thisContent:''
			}
		},
		methods:{
			clicked(index){
				this.thisIndex=index;
				this.thisContent=this.datas[index];
			}
		},
		mounted() {
			this.thisContent=this.datas[0];
		}
	}
</script>

