		width: 70%;
	}
	#deliciousLeft{
		margin-top: 4%;
		width: 30%;
		border: 1px solid #E6E6E6;
		-webkit-border-top-left-radius: 10px;
		box-shadow: -10px 5px 20px 0px #D8D8D8;
	}
	#deliciousLeftItem{
		position: relative;
		height: 9%;
		width: 100%;
		flex-direction: column;
