		border-bottom: 1px solid #D8D8D8;
	}


/* right */
	#deliciousRight{
		width: 70%;
		border: 1px solid #E6E6E6;
		border-radius: 20px;
		box-shadow: 5px 5px 30px 1px #D8D8D8;
